.company {
	position: relative;
	padding: 0 40px;
	max-width: 1680px;
	@include lg {
		padding: 0 20px; }
	&__container {
		position: relative; }
	&__header {
		position: absolute;
		top: 100px;
		left: 0;
		right: 0;
		padding: 20px;
		@include lg {
			position: relative;
			padding: 0 0 30px; } }
	&__title {
		margin: 0 auto;
		max-width: 800px;
		text-align: left; }
	&__slide {
		position: relative;
		padding-top: 100px;
		padding-left: 98px;
		padding-right: 98px;
		height: 100vh;
		min-height: 600px;
		@include clr;
		@include vertical;
		@include lg {
			padding: 0 0 40px;
			height: auto;
			min-height: auto; }
		&[data-id="1"] {
			padding-top: 300px;
			@include lg {
				padding-top: 0; }
			.company__subtitle {
				margin-bottom: 20px; }
			.company__inner {
				vertical-align {} } } }
	&__inner {
		display: inline-block;
		vertical-align: middle;
		width: 100%; }
	&__content {
		float: right;
		width: 100%;
		max-width: 380px;
		@include f(18px, 400, 28px);
		text-align: left;
		@include lg {
			float: none;
			max-width: 100%;
			@include f(13px, null, 18px); } }
	&__subtitle {
		margin-bottom: 12px;
		@include lg {
			margin-bottom: 20px; } }
	&__label {
		margin-bottom: 15px;
		@include lg {
			margin-bottom: 20px; } }
	&__dots {
		position: absolute;
		z-index: 20;
		top: 50%;
		right: 8px;
		opacity: 0;
		transition: opacity 0.5s;
		@include lg {
			position: relative;
			top: auto;
			right: auto;
			opacity: 1;
			text-align: center;
			pointer-events: none; }
		&.is-active {
			opacity: 1; }
		span {
			position: relative;
			display: block;
			width: 28px;
			height: 28px;
			cursor: pointer;
			@include lg {
				display: inline-block;
				vertical-align {}
				width: 14px;
				height: 14px; }
			&.is-active {
				&:before {
					background: rgba($black, 0.7); } }
			&:before {
				content: '';
				position: absolute;
				top: 9px;
				left: 9px;
				right: 9px;
				bottom: 9px;
				border-radius: 50%;
				opacity: 1;
				background: rgba($black, 0.2);
				@include lg {
					top: 4px;
					left: 4px;
					right: 4px;
					bottom: 4px; } } } }
	&__diagramm {
		position: absolute;
		top: 50%;
		left: 0;
		padding: 340px 40px 40px;
		width: calc(100% - 480px);
		max-width: 1000px;
		height: 100vh;
		max-height: 900px;
		@include vertical;
		white-space: nowrap;
		transform: translate3d(0,-50%,0) scale(0.9);
		transition: transform 0.5s, padding 0.5s;
		pointer-events: none;
		@include lg {
			position: relative;
			top: auto;
			left: auto;
			padding: 40px 0;
			margin: 0 auto;
			width: 100%;
			max-width: 360px;
			height: auto;
			transform: translate3d(0,0,0) scale(0.9); }
		&.is-active {
			padding-top: 140px;
			transform: translate3d(0,-50%,0) scale(1);
			@include lg {
				padding-top: 40px;
				transform: translate3d(0,0,0) scale(1); }
			[data-diagramm-inner] {
				opacity: 1; }
			.company__logo {
				padding-top: 140px;
				@include lg {
					padding-top: 40px; } } }
		&:before {
			@include lg {
				display: none; } }
		svg {
			display: inline-block;
			vertical-align: middle;
			max-width: 100%;
			max-height: 100%;
			transition: transform 0.7s;
			@include lg {
				display: block; } }
		[data-diagramm-inner] {
			opacity: 0;
			transition: opacity 0.5s; } }

	&__logo {
		position: absolute;
		top: 0;
		left: 0;
		right: 0;
		bottom: 0;
		padding: 340px 40px 40px;
		@include vertical;
		white-space: nowrap;
		@include gpu;
		transition: padding 0.5s;
		pointer-events: none;
		img {
			display: inline-block;
			vertical-align: middle;
			max-height: 16%; } } }
